.profile-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  column-gap: 2rem;
  padding: 2rem;
  margin: 0.5rem auto;
}
.teste-url {
  background-image: url('https://scontent-lhr8-2.cdninstagram.com/v/t51.2885-19/277816207_319644756822997_3505369846053847606_n.jpg?stp=dst-jpg_s150x150&_nc_ht=scontent-lhr8-2.cdninstagram.com&_nc_cat=101&_nc_ohc=NEYnuUM30n8AX9LV9o8&edm=AEhMeswBAAAA&ccb=7-5&oh=00_AT_HPPqAlOQ-u_uBRJ_x_LE3f19_6OY3rJzoJ8Vqwj-2-w&oe=62B90A06&_nc_sid=7fba47');
}

.profile-counts {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  column-gap: 2rem;
}

.profile-counts p {
  color: #000;
  font-size: 0.9rem;
  margin: 0.5rem 0;
}

.profile-picture {
  width: 8rem;
  height: 8rem;
  object-fit: contain;
  border-radius: 50%;
  background-color: #000;
}

.user-intro {
  padding: 1rem;
  width: 80%;
  margin: auto;
  row-gap: 0.25rem;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: flex-start;
}

.user-intro .bio {
  color: #000;
  font-size: 0.9rem;
}

.profile-picture-container {
  width: 80%;
  margin: 0 auto;
  padding: 1rem;
}

.fullsize-profile-picture {
  width: 100%;
  margin: 0 auto;
  border-radius: 1rem;
}

/*  Mobile  */

@media screen and (max-width: 520px) {
  .profile-picture {
    width: 3rem;
    height: 3rem;
  }
  .user-intro {
    width: auto;
  }
  .profile-picture-container {
    width: auto;
  }
  .profile-header {
    column-gap: 1rem;
  }
  .profile-response {
    padding: 0 1rem;
  }
}
