.button-seguidores{
  background: linear-gradient(
    45deg, #952bb6 0%,#af38a6 25%,#db4d8b 50%,#ec5681 75%,#ea5582 100%);
        border-radius: 5px;
  border-radius: 5px;
  padding: 15px;
  color: white;
  box-shadow: 2px 2px 4px 1px #1a1a1a45;
}
.button-seguidores:hover{
  background: #952bb6;
        border-radius: 5px;
  border-radius: 5px;
  padding: 15px;
  color: white;
  box-shadow: 2px 2px 4px 1px #1a1a1a45;
}
.button-seguidores a{
  color: white;
  font-weight: bold;
}