:root {
  --soft-light1: #585858;
  --soft-light2: #bbbbbb;
  --max-width: 1200px;
}

* {
  font-family: 'Montserrat', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
}
/* html{
    z-index: 999999999;
} */
body {
  background: rgb(158, 158, 158);
  color: rgb(4, 3, 3);
  overflow-x: hidden;
  /* z-index: 999999999; */
}

