.main-header {
  background-color: rgb(255, 255, 255);
  /* width: 60%; */
  margin: auto;
}

.web-header {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding: 1rem;
  column-gap: 1rem;
}
.link2 {
  display: inline-block;
}
.myLogo {
  width: 2.5rem;
  height: 2.5rem;
  object-fit: contain;
  text-align: center;
}

.active {
  background-color: #e8e8e8 !important;
}

.nav-buttons {
  padding: 0.5rem;
  background-color: inherit;
  border: none;
  outline: none;
  cursor: pointer;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 0.75rem;
  font-size: 0.8rem;
  width: 100%;
}
.header {
  background-color: white;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.206);
  justify-content: space-around;
  align-items: center;
  padding: 0.5rem;
  width: 100%;
}

.nav-icons {
  background-color: inherit;
  width: 1.6rem;
  height: 1.6rem;
  object-fit: cover;
  pointer-events: none;
}
@media (max-width: 525px) {
  .web-header {
    padding: 0rem;
    height: 13vh;
  }
  .nav-section {
    width: 100% !important;
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: row;
    width: 50%;
    margin: auto;
  }
  .nav-buttons {
    padding: 0.55rem;
    background-color: inherit;
    /* border: none; */
    /* outline: none; */
    cursor: pointer;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-row-gap: 0.75rem;
    /* row-gap: 0.75rem; */
    font-size: 0.8rem;
    width: 100%;
  }
  .button-seguidores {
    background: linear-gradient(
      45deg,
      #952bb6 0%,
      #af38a6 25%,
      #db4d8b 50%,
      #ec5681 75%,
      #ea5582 100%
    );
    border-radius: 5px;
    border-radius: 5px;
    color: white;
    box-shadow: 2px 2px 4px 1px #1a1a1a45;
    padding: 7px;
    font-size: 12px;
  }
}
.nav-name {
  pointer-events: none;
  font-weight: bolder;
  text-align: center;
  background: linear-gradient(
    45deg,
    #f09433 0%,
    #e6683c 25%,
    #dc2743 50%,
    #cc2366 75%,
    #bc1888 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

