.nav-section {
  background-color: rgb(255, 255, 255);
  display: flex;
  flex-direction: row;
  align-items: center;
  /* overflow-x: scroll; */
  /* list-style: none; */
  padding: 0px !important;
  /* margin: 15px auto 0 auto !important; */
  /* grid-column-gap: 0.9rem;
  grid-column-gap: 0.9rem;
  column-gap: 0.9rem; */
  /* width: 50%; */
  margin: auto;
}
.nav-section::-webkit-scrollbar {
  height: 5px;
}

.active-navigation {
  display: flex;
  justify-content: center;
  align-items: center;
  /* border-bottom: 4px solid #0000ce; */
  background: rgb(187, 187, 187);
  border-radius: 0.5rem;
}

.active-navigation * {
  color: #ffffff;
}

a:-webkit-any-link:active {
  color: none;
}

a {
  text-decoration: none;
}

.error-page {
  width: 100%;
  height: 50vh;
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.75rem;
  background-color: white;
}

.error-page * {
  color: #dedede;
  font-family: monospace;
  padding: 0.5rem 0;
}
.bio {
  color: rgb(0, 0, 0);
}
