.description {
  justify-content: center;
  text-align: center;
  background-color: #e8e8e8 !important;
}
.text-description {
  background: linear-gradient(
    45deg,
    #f09433 0%,
    #e6683c 25%,
    #dc2743 50%,
    #cc2366 75%,
    #bc1888 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  padding-top: 2.5rem;
  font-weight: bold;
}
.p-description {
  font-weight: 400 !important;
  padding-bottom: 1rem;
  padding-top: 0.8rem;
  margin-bottom: 0px !important;
}
.flex {
  display: flex;
}
