.reel-output {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.reel-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: nowrap;
  align-content: center;
  justify-content: space-between;
  padding: 1rem;
}

.reel-response,
.post-response {
  background-color: rgb(138, 138, 138);
  display: flex;
  justify-content: center;
  align-content: center;
  flex-direction: column;
  margin: 3rem auto;
  width: 35rem;
}

.reel-creator {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  column-gap: 2rem;
}

.reel-caption,
.post-caption {
  padding: 1rem;
  color: #000;
}

.reel-video,
.post-section {
  margin: auto;
  padding: 0 1rem;
  width: 100%;
}

.creator-info {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: flex-start;
  row-gap: 0.25rem;
  font-size: 0.85rem;
}

@media screen and (max-width: 520px) {
  .profile-picture {
    width: 2.5rem;
    height: 2.5rem;
  }
  .creator-info {
    font-size: 0.75rem;
  }
  .reel-creator {
    column-gap: 1rem;
  }
  .reel-video {
    width: 100%;
  }
  .reel-caption,
  .post-caption {
    padding: 1rem;
    font-size: 0.8rem;
  }
}

.buttonDownload {
  /* display: inline-block; */
  width: 60%;
  position: relative;
  padding: 10px 25px;
  background: linear-gradient(
    45deg,
    #f09433 0%,
    #e6683c 25%,
    #dc2743 50%,
    #cc2366 75%,
    #bc1888 100%
  );
  color: white;
  font-weight: bold;
  text-decoration: none;
  font-size: 0.9em;
  text-align: center;
  text-indent: 15px;
  cursor: pointer;
  border-radius: 8px;
  margin: 15px auto;
}
@media (max-width: 525px) {
  .buttonDownload {
    width: 90%;
    font-size: 12px !important;
    padding: 5px 15px;
  }
  .buttonDownload-photo {
    width: 90%;
    font-size: 12px !important;
    padding: 5px 15px;
  }
}
.buttonDownload-photo {
  display: inline-block;
  position: relative;
  padding: 10px 25px;
  background: linear-gradient(
    45deg,
    #f09433 0%,
    #e6683c 25%,
    #dc2743 50%,
    #cc2366 75%,
    #bc1888 100%
  );
  color: white;
  font-weight: bold;
  text-decoration: none;
  font-size: 0.9em;
  text-align: center;
  text-indent: 15px;
  cursor: pointer;
  border-radius: 8px;
  margin: 15px auto;
}

.buttonDownload:hover {
  background: linear-gradient(
    45deg,
    #a16423 0%,
    #964628 25%,
    #971b2e 50%,
    #831642 75%,
    #6b0d4d 100%
  );
  color: white;
}

.buttonDownload:before,
.buttonDownload:after {
  content: ' ';
  display: block;
  position: absolute;
  left: 15px;
  top: 52%;
}

/* Download box shape  */
.buttonDownload:before {
  width: 10px;
  height: 2px;
  border-style: solid;
  border-width: 0 2px 2px;
}

/* Download arrow shape */
.buttonDownload:after {
  width: 0;
  height: 0;
  margin-left: 3px;
  margin-top: -7px;

  border-style: solid;
  border-width: 4px 4px 0 4px;
  border-color: transparent;
  border-top-color: inherit;

  animation: downloadArrow 2s linear infinite;
  animation-play-state: paused;
}

.buttonDownload:hover:before {
  border-color: #4cc713;
}

.buttonDownload:hover:after {
  border-top-color: #4cc713;
  animation-play-state: running;
}
.buttonDownload-photo:hover {
  background: linear-gradient(
    45deg,
    #a16423 0%,
    #964628 25%,
    #971b2e 50%,
    #831642 75%,
    #6b0d4d 100%
  );
  color: white;
}

.buttonDownload-photo:before,
.buttonDownload-photo:after {
  content: ' ';
  display: block;
  position: absolute;
  left: 15px;
  top: 52%;
}

/* Download box shape  */
.buttonDownload-photo:before {
  width: 10px;
  height: 2px;
  border-style: solid;
  border-width: 0 2px 2px;
}

/* Download arrow shape */
.buttonDownload-photo:after {
  width: 0;
  height: 0;
  margin-left: 3px;
  margin-top: -7px;

  border-style: solid;
  border-width: 4px 4px 0 4px;
  border-color: transparent;
  border-top-color: inherit;

  animation: downloadArrow 2s linear infinite;
  animation-play-state: paused;
}

.buttonDownload-photo:hover:before {
  border-color: #4cc713;
}

.buttonDownload-photo:hover:after {
  border-top-color: #4cc713;
  animation-play-state: running;
}

/* keyframes for the download icon anim */
@keyframes downloadArrow {
  /* 0% and 0.001% keyframes used as a hackish way of having the button frozen on a nice looking frame by default */
  0% {
    margin-top: -7px;
    opacity: 1;
  }

  0.001% {
    margin-top: -15px;
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  100% {
    margin-top: 0;
    opacity: 0;
  }
}
